@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;600;700&display=swap);
.categories-container {
  position: absolute;
  top: 10vh;
  right: 2vw;
  width: 800px;
  height: 300px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background-color: #333;
  border-radius: 5px;
  overflow: hidden;
  padding: 15px;
  -webkit-animation: aparecer 1s ease;
          animation: aparecer 1s ease;
}

@-webkit-keyframes aparecer {
  0% {
    opacity: 0;
    right: 0vw;
  }
  100% {
    opacity: 1;
    right: 2vw;
  }
}

@keyframes aparecer {
  0% {
    opacity: 0;
    right: 0vw;
  }
  100% {
    opacity: 1;
    right: 2vw;
  }
}

.categories-container button {
  color: white;
  height: 30px;
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-right: 1px solid #4e5258;
}

.categories-container button:hover {
  color: var(--main-color);
}
header {
  width: 100vw;
  height: 10vh;
  top: 0;
  z-index: 10;
  position: fixed;
  display: flex;
  padding-left: 20px;
  align-items: center;
  justify-content: space-between;
  background-color: #2c2c2c;
}

header > a {
  height: 100%;
}

header nav {
  height: 100%;
  width: 30%;
  display: flex;
  align-items: center;
}

header nav a {
  display: flex;
  justify-content: center;
  width: 100%;
}

header nav .categories-header-container {
  width: 100%;
}

header nav .categories-header-container h3 {
  color: rgba(255, 255, 255, 0.6);
  font-size: 1.5em;
  text-align: center;
  transition: .5s ease;
  cursor: pointer;
}

header nav .categories-header-container h3:hover {
  color: white;
}

header nav a h3 {
  color: rgba(255, 255, 255, 0.6);
  font-size: 1.5em;
  transition: .5s ease;
  text-align: center;
}

header nav a h3:hover {
  color: white;
}

.logo-container {
  height: 100%;
  display: flex;
  align-items: center;
  color: white;
}

.logo-container img {
  height: 50%;
  border-radius: 50%;
  margin-right: 5px;
}

.cart-button-container {
  cursor: pointer;
  width: 100px;
  height: 48px;
  display: flex;
  align-items: center;
  background-color: white;
  border-radius: 50px;
}

.cart-button-container button {
  cursor: pointer;
  display: grid;
  place-items: center;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: var(--main-color);
  color: white;
  font-size: 1.5em;
  border: none;
}

.cart-button-container p {
  width: 40px;
  text-align: center;
  color: var(--main-color);
  font-size: 1.5em;
}
.products-container {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5vh;
}

.flex-container {
  width: 80%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.product {
  background-color: white;
  border-radius: 6px;
  border: 1px solid #E1E1E1;
  display: flex;
  flex-direction: column;
  height: 500px;
  overflow: hidden;
  margin: 10px;
  position: relative;
  transition: .5s ease;
  width: 290px;
}

.product:hover {
  box-shadow: 8px 8px 5px rgba(0, 0, 0, 0.2);
}

.product img {
  width: 100%;
  height: 288px;
  border-bottom: 1px solid #E1E1E1;
}

.product button {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 48px;
  height: 48px;
  display: grid;
  place-items: center;
  background-color: white;
  border: 3px solid #2fc18c;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 3px 3px rgba(0, 0, 0, 0.2);
  -webkit-animation: cartBot .5s ease;
          animation: cartBot .5s ease;
  font-size: 1.5em;
  color: #2fc18c;
}

.product button:active {
  box-shadow: none;
}

@-webkit-keyframes cartBot {
  0% {
    opacity: 0;
    right: 0;
  }
  100% {
    opacity: 1;
    right: 10px;
  }
}

@keyframes cartBot {
  0% {
    opacity: 0;
    right: 0;
  }
  100% {
    opacity: 1;
    right: 10px;
  }
}

.product-information {
  width: 100%;
  height: 200px;
  padding: 15px;
}

.frete {
  font-size: 1em;
  font-weight: 600;
  color: #2fc18c;
}

.product-information p, .product-information h2 {
  margin-bottom: 5px;
}

main {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-title-container {
  width: 100vw;
  height: 80vh;
  margin-top: 10vh;
  background-image: url(https://ply3r.github.io/portifolio_antigo/projetos/loja_de_roupa/imagens/wallpaper.jpg);
  background-size: 100% 100%;
  background-attachment: fixed;
  background-repeat: no-repeat;
  display: grid;
  place-items: center;
}

.hero-title {
  font-size: 5em;
  font-weight: 700;
  color: white;
}

.shop-title {
  margin-top: 2vh;
}

.search-container {
  width: 100vw;
  height: 5vw;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;
}

.search-container input {
  height: 5vh;
  width: 50vw;
  border-radius: 30px;
  font-size: 1.5em;
  padding: 0 20px;
}

.cart-title-container {
  width: 100vw;
  display: grid;
  place-items: center;
}

.cart-page {
  margin-top: 10vh;
  width: 100vw;
  height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cart-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 60vw;
  height: 65vh;
  background-color: white;
  border-radius: 6px;
}

.total-price {
  margin: 25px 0;
  width: 300px;
}

.cartItens-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60vw;
  height: 60vh;
  border-radius: 6px;
  overflow: auto;
  padding: 10px 0;
  border: 2px solid #E1E1E1;
}

.cartItem {
  width: 100%;
  height: 15vh;
  display: flex;
  align-items: center;
  padding: 15px;
  border-bottom: 2px solid #E1E1E1;
}

.cartItem a {
  width: 60%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.cartItem img {
  margin: 0 20px;
}

.cartItem-title-container {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 15px;
}

.cartItem-title-container button {
  background-color: transparent;
  border: none;
  color: red;
  cursor: pointer;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.quantity-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15%;
}

.quantity-container div {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  place-items: center;
  width: 80px;
  height: 40px;
  border: 1px solid #E1E1E1;
  border-radius: 3px;
}

.quantity-container div button {
  cursor: pointer;
  background-color: transparent;
  color: #0069D9;
  font-size: 1.2em;
  border: none;
  height: 100%;
  width: 100%;
}

.quantity-container div button:disabled {
  color: #cde5ff;
}

.checkout-products {
  cursor: pointer;
  border-radius: 6px;
  border: none;
  color: white;
  background-color: #157347;
  font-size: 1.2em;
  margin: 2vh 0;
  height: 60px;
  width: 200px;
}

.checkout-products:disabled {
  background-color: #4b8067;
}

.product-page {
  margin-top: 10vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.image-information-product-container {
  display: flex;
  margin-top: 3vh;
  width: 65%;
  height: 80vh;
  border-radius: 10px;
  border: 2px solid #E1E1E1;
  background-color: white;
}

.image-product-container {
  height: 100%;
  width: 60%;
  display: grid;
  place-items: center;
}

.image-product-container img {
  width: 70%;
  border: 1px solid #E1E1E1;
  box-shadow: 10px 10px rgba(0, 0, 0, 0.2);
}

.information-product-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 40%;
  height: 100%;
  padding: 25px;
}

.information-product-container h1 {
  font-size: 1.7em;
}

.information-product-container h1,
.information-product-container h3,
.information-product-container p {
  margin-bottom: 15px;
}

.add-cart-bot-product {
  background-color: #0069D9;
  border: none;
  height: 5vh;
  border-radius: 10px;
  font-size: 1.2em;
  cursor: pointer;
  color: white;
}

.add-cart-bot-product:hover {
  background-color: #0059b8;
}

.add-cart-bot-product:active {
  font-size: 1.18em;
}

.star-container {
  color: #0069D9;
}

.form-container {
  display: flex;
  flex-direction: column;
  padding: 25px;
  margin: 5vh 0;
  width: 65%;
  border-radius: 10px;
  border: 2px solid #E1E1E1;
  background-color: white;
}

.form-container h1 {
  margin: 15px 0;
}

.form-container .email {
  padding: 5px;
  font-size: 1.5em;
  width: 100%;
  height: 5vh;
  margin-bottom: 15px;
}

.form-container textarea {
  height: 10vh;
  padding: 5px;
  width: 100%;
  resize: none;
}

.avaliation-container {
  margin-bottom: 15px;
}

.avaliation-container button {
  background-color: white;
  border: none;
  font-size: 1.5em;
  cursor: pointer;
  color: #0069D9;
}

.loading-container {
  width: 100vw;
  display: flex;
  justify-content: center;
}

.review-bot {
  margin: 15px 0;
}

.review-rate-container {
  margin-top: 15px;
  color: #0069D9;
  font-size: 1.5em;
}

.page-checkout {
  width: 100vw;
  margin-top: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.checkout-info-container {
  display: flex;
  flex-direction: column;
  width: 65%;
  height: 60vh;
  background-color: white;
  border-radius: 15px;
  margin-bottom: 5vh;
  overflow: hidden;
  border-bottom: 2px solid #E1E1E1;
}

.checkout-title-container {
  width: 75%;
}

.checkout-products-container {
  border-bottom: 2px solid #E1E1E1;
  border-radius: 6px;
  overflow: auto;
  width: 100%;
  height: 90%;
}

.checkout-total-price {
  margin-top: 10px;
  width: 300px;
  align-self: flex-end;
}

.checkout-form {
  display: flex;
  flex-direction: column;
}

.checkout-form input {
  width: 100%;
  height: 5vh;
  margin-bottom: 15px;
  font-size: 1.5em;
  padding: 5px;
}

.checkout-form a {
  align-self: center;
}

.radio-containers {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 4vh;
}

.radio-containers img {
  width: 275px;
  height: 183px;
}

.radio-containers input {
  height: 2vh;
}

.checkout-page-bot {
  align-self: center;
}
:root {
  --main-color: #CC2936;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
}

body, #root, html {
  width: 100vw;
  background-color: #36393B;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: unset;
}
